.dataTableHeader {
    font-size: 0.875rem;
    color: #283a36;
    text-transform: uppercase;
    background-color: #253234;
}

.dataTableHeader th {
    padding: .75rem 1.5rem;
}

.table {
    position: relative;
    overflow-x: auto;
    margin: 4rem 6rem;
    padding-bottom: 2rem;
}

.tableTitle {
    margin: 8rem;
    color: #689d94;
    font-family: Quicksand, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tableTitle h1 {
    font-size: 2.25rem;
    font-weight: 600;
    margin-bottom: 1rem;
}

.tableTitle h2 {
    font-size: 1.25rem;
}

.tableTitle div {
    border-radius: 1rem;
    background-color: #689d94;
    height: 0.25rem;
    margin-top: 4rem;
}

.tableBody {
    border-radius: 1rem;
    position: relative;
    overflow-x: auto;
}

.tableBody table {
    width: 100%;
    font-size: .875rem;
    text-align: left;
    color: #689d94;
    padding-bottom: 2rem;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    border-radius: .5rem;
}

.tableBody .dataTableBody {
    background-color: white;
    border-bottom: 1px #283a36;
}

.tableBody .dataTableBody:hover {
    border-color: #1c2728;
}

.tableBody .dataTableBody th {
    padding: 1rem 1.5rem;
    font-weight: 500;
    color: #689d94;
    white-space: nowrap;
}

.tableBody .dataTableBody td {
    width: 1rem;
    padding: 1rem;
}

.tableBody .dataTableBody td .inputWithConversion {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tableBody .dataTableBody td .inputWithConversion span {
    margin-left: 1rem;
    color: white;
}

.tableBody .dataTableBody .inputBox {
    display: flex;
    align-items: center;
}

.tableBody .dataTableBody .inputBox input {
    background-color: #253234;
    border: 1px solid #283a36;
    color: #689d94;
    border-radius: 1rem;
}

.tableBody .dataTableBody .inputBox input .w-10 {
    width: 2.5rem;
}

.tableBody .dataTableBody .inputBox input .w-20 {
    width: 5rem;
}
